:root {
  --color-text: #1f2327;
  --color-accent: rgb(3, 163, 255);
  --color-header-bg: #1f2327;
  --color-gray-light: rgb(219, 219, 219);
  --color-gray-lighter: rgb(228, 228, 228);
  --color-gray-darker: rgb(182, 182, 182);

  // misc

  --header-height: 65px;
  --transition-params: ease-out 250ms;
  --loader-padding-top: 50px;
  --container-padding: 40px;
  --filter-expander-heigth: 35px;

  // search form

  --search-form-border-radius: 15px;
  --search-form-width: 400px;

  --search-input-outline-width: 1.5px;
  --search-input-outline: 0px 0px 0px var(--search-input-outline-width)
    var(--color-accent);

  --search-input-height: 75%;
  --search-input-padding: 15px;
  --search-input-padding-right: calc(
    var(--search-btn-width) + var(--search-btn-celar-width) +
      var(--search-btn-celar-width) / 2
  );

  --search-btn-width: 50px;
  --search-btn-scale: 1;

  --search-clear-btn-width: 28px;
  --search-clear-btn-offset: 5px;
  --search-clear-btn-right: calc(
    var(--search-btn-width) + var(--search-clear-btn-offset)
  );

  // gallery

  --gallery-items-gap: 10px;
  --gallery-items-per-row: 1;

  --overlay-bg: linear-gradient(0deg, rgba(0, 0, 0, 0.8) 0, transparent);
  --overlay-upper-bg: linear-gradient(
    180deg,
    rgba(0, 0, 0, 0.8) 0,
    transparent
  );
}

// breakpoints

$bp-mobile: 320px;
$bp-tablet: 768px;
$bp-desktop: 1200px;
