.backtop {
  @extend %link;

  position: fixed;
  bottom: 10px;
  right: 10px;
  z-index: 9;

  display: flex;
  justify-content: center;
  align-items: center;

  width: 50px;
  height: 50px;

  color: white;
  background-color: var(--color-accent);
  border-radius: 50%;
  opacity: 0.3;

  transition: opacity var(--transition-params);

  &:hover,
  &:focus-visible {
    opacity: 1;
  }

  &--hidden {
    display: none;
  }
}
